
import EncounterMixinVue from "@/views/EncounterMixin.vue";
import { FieldType } from "@/components/Forms/BaseFormElements";
import { defineComponent } from "vue";
import { IonPage } from "@ionic/vue";
import { Field, Option } from "@/components/Forms/FieldInterface";
import { find, isEmpty, uniqBy } from "lodash";
import keyboard, { numericKeypad, selectionList } from "@/utils/PopupKeyboard";
import { ObsValue } from "@/services/observation_service";
import PersonField from "@/utils/HisFormHelpers/PersonFieldHelper";
import dayjs from "dayjs";
import { PatientRegistrationService } from "@/services/patient_registration_service";
import { Patientservice } from "@/services/patient_service";
import { HtsItemsGivenService } from "../../services/hts_items_given";
import { toDate } from "@/utils/Strs";
import DateHelper from "@/utils/Date"
import { alertConfirmation, toastWarning } from "@/utils/Alerts";

export default defineComponent({
  components: { IonPage },
  mixins: [EncounterMixinVue],
  data: () => ({
    service: {} as any,
  }),
  watch: {
    ready: {
      handler() {
        this.service = new HtsItemsGivenService(-1);
        this.fields = [
          ...[
            this.accessPointTypeField(),
            this.facilityAccessPointField(),
            this.communityAccessPoints(),
            this.genderField(),
            this.ageField(),
            this.pregnancyStatusField(),
            this.lastHivTestResultField(),
            this.lastTimeHIVTestedField(),
            this.itemsGivenToClientField(),
            this.selfTestsGivenToClientField(),
            this.hivTestKitDetailsField(),
            this.visitTypeField(),
          ],
          ...this.patientDemographics(),
        ];
      },
      immediate: true,
    },
  },
  methods: {
    async onFinish(_: any, computedData: any) {
      const person = await PersonField.resolvePerson(computedData);
      const registration: any = new PatientRegistrationService();
      new Patientservice(await registration.registerBasicPatient(person));
      this.service.patientID = registration.getPersonID();
      await this.service.createEncounter();
      await this.service.saveObservationList(
        (await this.resolveObs(computedData, "reg")) as ObsValue[]
      );
      this.$router.push(`/`);
    },
    getSelfKitCount(f: Record<string, any>) {
      return (
        find(f.items_given_to_client || [], { label: "Self-Test Kit" })
          ?.value || 0
      );
    },
    accessPointTypeField(): Field {
      return {
        id: "accesspoint_type",
        helpText: "Distribution point type",
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        finalComputedValue: (v: Option) => {
          return {
            tag: "reg",
            obs: this.service.buildValueCoded("HTS Access Type", v.value),
          };
        },
        options: () => {
          return this.mapStrToOptions(["Health Facility", "Community"]);
        },
      };
    },
    facilityAccessPointField(): Field {
      return {
        id: "facility_access_points",
        helpText: "Health facility distribution point",
        type: FieldType.TT_SELECT_GRID,
        isRequired: () => true,
        condition: (f: any) => f.accesspoint_type.value === "Health Facility",
        finalComputedValue: (v: Option) => {
          return {
            tag: "reg",
            obs: this.service.buildValueText(
              "Location where test took place",
              v.value.toString()
            ),
          };
        },
        config: {
          columnsPerRow: 2
        },
        options: () => {
          return this.toConditionalOptions([
            ["VCT"],
            ["ANC First Visit"],
            ["Inpatient"],
            ["STI"],
            ["PMTCT FUP"],
            ["Index"],
            ["Paediatric"],
            ["VMMC"],
            ["Malnutrition"],
            ["TB"],
            ["OPD"],
            ["Other PITC"],
            ["SNS"],
          ]);
        },
      };
    },
    communityAccessPoints(): Field {
      return {
        id: "community_access_points",
        helpText: "Community distribution points",
        type: FieldType.TT_SELECT,
        condition: (f: any) => f.accesspoint_type.value === "Community",
        isRequired: () => true,
        finalComputedValue: (v: Option) => {
          return {
            tag: "reg",
            obs: this.service.buildValueText(
              "Location where test took place",
              `${v.value}`
            ),
          };
        },
        options: () => {
          return this.toConditionalOptions([
            ["VCT"],
            ["Index"],
            ["VMMC"],
            ["Other"],
            ["SNS"],
            ["Mobile"],
          ]);
        },
      };
    },
    genderField(): Field {
      return {
        id: "gender",
        helpText: "Sex",
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        finalComputedValue: (v: any) => {
          return {
            person: v.value,
          };
        },
        options: () => {
          return [
            { label: "Female", value: "F" },
            { label: "Male", value: "M" },
          ];
        },
      };
    },
    ageField(): Field {
      return {
        id: "birthdate",
        helpText: "Recipient age",
        type: FieldType.TT_NUMBER,
        isRequired: () => true,
        validation: (v: Option) => {
          if (/unknown/i.test(`${v.value}`)) return null
          if (v.value <= 10 || v.value >= 125 || isNaN(v.value as number)) {
            return ["Invalid Age"];
          }
          return null;
        },
        finalComputedValue: (v: any) => {
          return {
            person: {
              birthdate: DateHelper.estimateDateFromAge(v.value),
              birthdate_estimated: true,
            },
          };
        },
      };
    },
    pregnancyStatusField(): Field {
      return {
        id: "pregnancy_status",
        helpText: "Pregnancy Status",
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        condition: (f: any) => f.gender.value === "F",
        finalComputedValue: (v: Option) => {
          return {
            tag: "reg",
            obs: this.service.buildValueCoded("Pregnancy status", v.value),
          };
        },
        options: () => {
          return [
            { label: "Non-pregnant", value: "Not Pregnant / Breastfeeding" },
            { label: "Pregnant", value: "Pregnant woman" },
            { label: "Breastfeeding", value: "Breastfeeding" },
          ];
        },
      };
    },
    lastHivTestResultField(): Field {
      return {
        id: "last_hiv_result",
        helpText: "Recipient's Last HIV Test Results",
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        finalComputedValue: (v: any) => {
          return {
            tag: "reg",
            obs: this.service.buildValueCoded(
              "Previous HIV Test Results",
              v.value
            ),
          };
        },
        options: () => {
          return this.mapStrToOptions([
            "Never Tested",
            "Negative",
            "Positive NOT on ART",
            "Positive on ART",
            "Inconclusive",
          ]);
        },
      };
    },
    lastTimeHIVTestedField(): Field {
      return {
        id: "last_time_hiv_tested",
        helpText: "Time since last HIV test",
        type: FieldType.TT_AGE_INPUT,
        condition: (f: any) =>
          f.last_hiv_result.value && f.last_hiv_result.value != "Never Tested",
        isRequired: () => true,
        validation: (v: any, f: any) => {
          if (parseInt(`${v.other.value}`) <= 0 && !/day/i.test(v.other.timeUnit)) {
            return [`Enter ${v.other.timeUnit} greater than zero`];
          }
          if (
            dayjs(v.other.date).isBefore(
              dayjs().subtract(parseInt(f.birthdate.value), "year"),
              "year"
            )
          ) {
            return ["Enter a date that is not before the patient's birthdate"];
          }
          if (v.other.value.length > 1 && v.other.value[0] === "0") {
            return [`Value ${v.other.value} is invalid!`];
          }
          return null;
        },
        finalComputedValue: (v: any) => {
          return {
            tag: "reg",
            obs: this.service.buildObs("Time of HIV test", {
              'value_datetime': v.other.date,
              'value_text': v.value
            })
          }
        },
        config: {
          excludeUnits: ["Hours"],
        },
      };
    },
    itemsGivenToClientField(): Field {
      return {
        id: "items_given_to_client",
        helpText: "Items given to the client",
        type: FieldType.TT_ADHERENCE_INPUT,
        isRequired: () => true,
        summaryMapValue: (v: Option) => v,
        validation: (v: Option[]) => {
          if (v.every((d) => d.value === "")) {
            return ["Values cannot be empty"];
          }
          if (
            v.some(
              (d) => /self-test/i.test(d.label) && (!d.value || d.value <= 0)
            )
          ) {
            return [`Please enter number of self kits to give client`];
          }
          const maxSelfKits = 3;
          const maxMaleCondoms = 30;
          const maxFemaleCondoms = 30;
          const maxFRS = 5;
          if (
            v.some((d) => /self-test/i.test(d.label) && d.value > maxSelfKits)
          ) {
            return [
              `Cannot distribute more than ${maxSelfKits} Self-test kits. Please adjust the number accordingly`,
            ];
          }
          if (
            v.some((d) => /male condoms/i.test(d.label.toLowerCase()) && d.value > maxMaleCondoms)
          ) {
            return [
              `Cannot distribute more than ${maxMaleCondoms} Male Condoms. Please adjust the number accordingly`,
            ];
          }if (
            v.some((d) => /female condoms/i.test(d.label.toLowerCase()) && d.value > maxFemaleCondoms)
          ) {
            return [
              `Cannot distribute more than ${maxFemaleCondoms} Female Condoms. Please adjust the number accordingly`,
            ];
          }if (
            v.some((d) => /frs/i.test(d.label.toLowerCase()) && d.value > maxFRS)
          ) {
            return [
              `Cannot distribute more than ${maxFRS} FRS. Please adjust the number accordingly`,
            ];
          }
          return null;
        },
        options: (f: any) => {
          if (!isEmpty(f.items_given_to_client)) {
            return f.items_given_to_client;
          }
          return [
            { label: "Self-Test Kit", value: "" },
            { label: "Male condoms", value: "" },
            { label: "Female condoms", value: "" },
            { label: "FRS", value: "" },
          ] as Option[];
        },
        finalComputedValue: (v: Option[]) => {
          return {
            tag: "reg",
            obs: v
              .filter((d: Option) => d.value)
              .map((d: Option) =>
                this.service.buildValueNumber(d.label, parseInt(`${d.value}`))
              ),
          };
        },
        config: {
          noFloat: true,
          titles: {
            label: "Item",
            value: "Quantity",
          },
        },
      };
    },
    hivTestKitDetailsField(): Field {
      let kitInformationUpdated = false
      const SESSION_KIT_NAMES = "htsSelfKitNames";
      const SESSION_KIT_ID = "htsSelfTestKitData";
      let kitHistory: any = JSON.parse(
        sessionStorage.getItem(SESSION_KIT_ID) || "{}"
      );
      let kitOptions: Option[] | [] = JSON.parse(
        sessionStorage.getItem(SESSION_KIT_NAMES) || "[]"
      );
      const getSessionKitValue = (id: string) => kitHistory[id] || "";
      return {
        id: "test_kit",
        helpText: "Kit information",
        type: FieldType.TT_INPUT_ARRAY,
        //summaryMapValue: (v: Option) => v?.other?.fields,
        config: {
          columns: [
            { label: "Kit Name" },
            { label: "Kit Lot No." },
            { label: "Kit Expiry Date" },
          ],
        },
        isRequired: () => true,
        summaryMapValue: (v: Option) => v?.other?.fields,
        validation: (v: Option[]) => {
          const incomplete = v
            .map((d: Option) => d.other.fields)
            .some((d: Option[]) => d.some((x: Option) => x.value === ""));
          if (incomplete) {
            return [`Some rows are missing data`];
          }
          return null;
        },
        beforeNext: async () => {
          if (!kitInformationUpdated && !isEmpty(kitHistory)) {
            if (!(await alertConfirmation('Are you sure you want to proceed without updating testkits?'))) {
              return false
            }
          }
          return true
        },
        finalComputedValue: (v: Option[]) => {
          const data = v.map((d) => d.other.fields);
          // Cache stuff in session storage taking advantage when creating computed values
          sessionStorage.setItem(SESSION_KIT_NAMES, JSON.stringify(kitOptions));
          sessionStorage.setItem(
            SESSION_KIT_ID,
            JSON.stringify({
              kitName: find(data[0], { label: "Kit Name" })?.value,
              kitLot: find(data[0], { label: "Kit Lot No." })?.value,
              kitExpiry: find(data[0], { label: "Kit Expiry Date" })?.value,
            })
          );
          return {
            obs: data.map(async (options: Option[]) => {
              const [parentObs, ...childObs] = options
                .filter((l: Option) => typeof l.other.obs === "function")
                .map((d: Option) => d.other.obs(d));
              return {
                ...(await parentObs),
                child: await Promise.all(childObs),
              };
            }),
          };
        },
        options: async (f: any) => {
          if (f.test_kit != null) return f.test_kit;
          const kitNameOption = () => ({
            label: "Kit Name",
            value: getSessionKitValue("kitName"),
            other: {
              obs: (v: Option) =>
                this.service.buildValueText("Kit name", v.value as string),
              onclick: (val: Option, r: any, index: any, t: any, listData: Option[]) => {
                keyboard(
                  {
                    id: "name",
                    helpText: "Kit Name",
                    type: FieldType.TT_TEXT,
                    isRequired: () => true,
                    options: () => kitOptions,
                  },
                  (v: Option) => {
                    val.value = v.value;
                    kitOptions = uniqBy([v, ...kitOptions], "label");
                    kitInformationUpdated = true
                    if (index === 0) {
                      listData.forEach((i: Option) => {
                        i.other.fields.forEach((d: Option) => {
                          if (/name/i.test(d.label) && d.value === '') {
                            d.value = val.value
                          }
                        })
                      })
                    }
                  }
                );
              },
            },
          });
          const kitLotNoOption = () => ({
            label: "Kit Lot No.",
            value: getSessionKitValue("kitLot"),
            other: {
              obs: (v: Option) =>
                this.service.buildValueNumber(
                  "Kit lot number",
                  v.value as number
                ),
              onclick: (val: Option, r: any, index: any, t: any, listData: Option[]) => {
                keyboard({
                  id: 'lot',
                  helpText: 'Enter Lot Number',
                  type: FieldType.TT_TEXT,
                  isRequired: () => true
                }, 
                (v: Option) => {
                  val.value = v.value
                  kitInformationUpdated = true
                  // Use first row's Lot number to update other Lot numbers which are empty
                  if (index === 0) {
                    listData.forEach((i: Option) => {
                      i.other.fields.forEach((d: Option) => {
                        if (/lot/i.test(d.label) && d.value === '') {
                          d.value = val.value
                        }
                      })
                    })
                  }
                })
              },
            },
          });
          const kitExpiryDateOption = () => ({
            label: "Kit Expiry Date",
            value: getSessionKitValue("kitExpiry"),
            other: {
              obs: (v: Option) =>
                this.service.buildValueDate(
                  "kit expiry date",
                  v.value as string
                ),
              onclick: (val: Option, r: any, index: any, t: any, listData: Option[]) => {
                keyboard(
                  {
                    id: "end_user",
                    helpText: "Kit Expiry Date",
                    type: FieldType.TT_FULL_DATE,
                    isRequired: () => true,
                    validation: (v: Option) => {
                      if (new Date(v.label) < new Date(this.service.date)) {
                        return [
                          "Expiry date cannot be less than current date " +
                            toDate(this.service.date),
                        ];
                      }
                      const monthsFromNow = dayjs(v.label).diff(this.service.date, "month")
                      if (monthsFromNow <= 2) {
                        return ["It's not advisable to use a test kit that will expire in less than 2 months"]
                      }
                      return null;
                    },
                  },
                  (v: Option) => {
                    val.value = v.value
                    kitInformationUpdated = true
                    // Use first row's expiry date to update other expiry dates
                    if (index === 0) {
                      listData.forEach((i: Option) => {
                        i.other.fields.forEach((d: Option) => {
                          if (/expiry/i.test(d.label) && d.value === '') {
                            d.value = val.value
                          }
                        })
                      })
                    }
                  }
                );
              },
            },
          })
          const kits = this.getSelfKitCount(f);
          const generateRows = (count = kits) =>
            Array.from({ length: count }, () => {
              return {
                label: "",
                value: "",
                other: {
                  fields: [
                   kitNameOption(),
                   kitLotNoOption(),
                   kitExpiryDateOption(),
                  ],
                },
              };
            });
            if (Array.isArray(f.test_kit)) {
              if (kits < f.test_kit.length) {
                return f.test_kit.slice(0, kits);
              } else if (kits > f.test_kit.length) {
                return [
                  ...f.test_kit,
                  ...generateRows(
                    kits - f.test_kit.length
                  ),
                ];
              } else {
                return f.test_kit;
              }
          }
          return generateRows();
        },
      };
    },
    selfTestsGivenToClientField(): Field {
      return {
        id: "self_test_kits",
        helpText: "Self-test kit users",
        type: FieldType.TT_INPUT_ARRAY,
        isRequired: () => true,
        condition: (f: any) =>
          f.items_given_to_client.some(
            (d: Option) => /self-test/i.test(d.label) && d.value > 0
          ),
        finalComputedValue: (v: Option[]) => {
          return {
            tag: "reg",
            obs: v
              .map((d) => d.other.fields)
              .map(async (options: Option[]) => {
                const [parentObs, ...childObs] = options
                  .filter((l: Option) => typeof l.other.obs === "function")
                  .map((d: Option) => d.other.obs(d.value, d));
                return {
                  ...(await parentObs),
                  child: await Promise.all(childObs),
                };
              }),
          };
        },
        summaryMapValue: (v: Option) => v.other.fields.map((d: Option) => {
          const el = {...d}
          if (el.label === 'Age' && el?.other?.estimate) {
            el.value = `${el.value} (Estimated)`
          }
          return el
        }),
        config: {
          columns: [
            { label: "End-User" },
            { label: "Sex" },
            { label: "Age" },
            { label: "Approach" },
            { label: "Result"}
          ],
        },
        validation: (v: Option[]) => {
          const data = v.map((d: Option) => d.other.fields);
          const someIncomplete = data.some((d: Option[]) => {
            const count = d.filter((x: Option) => x.value != "").length;
            return count > 0 && count < 4;
          });
          if (someIncomplete) {
            return ["Some rows have incomplete data!"];
          }
          const incomplete = data.some((d: Option[]) => {
            return d.some((x: Option) => x.value === "");
          });
          if (incomplete) {
            return [`Some rows are missing data`];
          }
          return null;
        },
        options: (f: any) => {
          const broadcast = (rows: Option[], channel: string, newValue: string|number, oldValue: string|number) => {
            rows.forEach(col => {
              if (typeof col?.other?.broadcastlistener === "function") {
                const update = col.other.broadcastlistener(channel, newValue, oldValue)
                if (update) {
                  col.value = update.value
                  col.disabled = update.disabled
                }
              }
            })
          }
          const userOptionField = () => ({
            label: "User",
            value: "",
            other: {
              obs: (v: string) => this.service.buildValueCoded("Self-test end user", v),
              onclick: (
                val: Option,
                rows: Option[],
                index: number,
                i: number,
                listData: Option[]
              ) => {
                const otherRows = listData.filter((_: any, i) => i != index);
                const options = [];
                if (!/Self/.test(JSON.stringify(otherRows))) {
                  options.push("Self");
                }
                selectionList(
                  [...options, "Sexual partner", "Other"],
                  (selection: string) => {
                    broadcast(rows, 'User', selection, val.value)
                    val.value = selection;
                  },
                  {
                    title: "Select End-User",
                    preset: val != null ? val.value : "",
                    size: "small-modal",
                  }
                );
              },
            },
          });
          const genderOptionField = () => ({
            label: "Sex",
            value: "",
            other: {
              broadcastlistener: (channel: string, newValue: string, oldValue: string) => {
                if (channel === 'User') {
                  if (newValue === 'Self') {
                    return { value: f.gender.label, disabled: true }
                  }
                  if (oldValue === 'Self') {
                    return { value: '', disabled: false }
                  }
                }
              },
              obs: (v: string) => this.service.buildValueCoded("Gender of contact", v),
              onclick: (val: Option) => {
                selectionList(
                  ["Female", "Male"],
                  (selection: string) => val.value = selection,
                  {
                    title: "Select sex",
                    size: "small-modal",
                    preset: val != null ? val.value : "",
                  }
                )
              }
            },
          });
          const resultOptionField = () => ({
            label: "Result",
            value: "N/A",
            other: {
              obs: (v: string) => {
                if (v && !/n\/a/i.test(`${v}`)) {
                  return this.service.buildValueCoded('Test 1', v)
                }
              },
              onclick: (val: Option, rows: Option[]) => {
                selectionList([
                  'Negative',
                  'Positive',
                  'N/A'
                ], 
                (selection: string) => {
                  broadcast(rows, 'Result', selection, val.value)
                  val.value = selection
                }, {
                  title: 'Directly-assisted HIV Result'
                })
              }
            }
          })
          const ageOptionField = () => ({
            label: "Age",
            value: "",
            other: {
              broadcastlistener: (channel: string, newValue: string) => {
                if (channel === 'User') {
                  if (newValue === 'Self') {
                    return { value: `${f.birthdate.value}`, disabled: true }
                  }
                  return { value: "", disabled: false }
                }
              },
              obs: (v: string, d: Option) => {
                const age = parseInt(v)
                if (d?.other?.estimate) {
                  return this.service.buildNumericEstimate('Age of contact', age)
                } else {
                  const estimatedDOB = DateHelper.estimateDateFromAge(age)
                  return this.service.buildObs("Age of contact", {
                      'value_datetime': estimatedDOB,
                      'value_numeric': age
                  })
                }
              },
              onclick: (val: Option) => {
                numericKeypad((age: number | string, isEstimate: boolean | undefined) => {
                  if (age && parseInt(`${age}`) >= 125) {
                    val.value =''
                    return toastWarning(`Age exceeds maximum human life expectancy of 124 years`)
                  }
                  val.value = age
                  val.other.estimate = isEstimate
                }, 
                {
                  title: 'Age',
                  preset: ' ',
                  toggleButton: "Age is estimated?",
                  noFloat: true,
                  strictNumbers: true
                });
              },
            },
          });
          const approachOptionField = () => ({
            label: "Approach",
            value: "",
            other: {
              broadcastlistener: (channel: string, newValue: string) => {
                if (channel === 'User') {
                  if (newValue != 'Self') {
                    return { value: "Un-assisted", disabled: true }
                  }
                  return { value: "", disabled: false }
                }
              },
              obs: (v: string) => this.service.buildValueCoded("Self-test approach", v),
              onclick: (val: Option, rows: any[]) => {
                selectionList(
                  ["Directly-assisted", "Un-assisted"],
                  (v: string) => {
                    broadcast(rows, 'Approach', v, val.value)
                    val.value = v
                  },
                  {
                    title: "Select approach",
                    size: "small-modal",
                    preset: val != null ? val.value : "",
                  }
                );
              },
            },
          });
          const kits = this.getSelfKitCount(f);
          const generateRows = (count = kits) =>
            Array.from({ length: count }, () => {
              return {
                label: "item",
                value: "N/A",
                other: {
                  fields: [
                    userOptionField(),
                    genderOptionField(),
                    ageOptionField(),
                    approachOptionField(),
                    resultOptionField()
                  ],
                },
              };
            });
          if (Array.isArray(f.self_test_kits)) {
            if (kits < f.self_test_kits.length) {
              return f.self_test_kits.slice(0, kits);
            } else if (kits > f.self_test_kits.length) {
              return [
                ...f.self_test_kits,
                ...generateRows(
                  kits - f.self_test_kits.length
                ),
              ];
            } else {
              return f.self_test_kits;
            }
          }
          return generateRows();
        },
      };
    },
    visitTypeField(): Field {
      return {
        id: "visit_type",
        helpText: "Visit Type",
        type: FieldType.TT_HIDDEN,
        isRequired: () => true,
        computedValue: (v: Option) => {
          return {
            tag: 'reg',
            obs: this.service.buildValueCoded("Visit type", "Self test distribution")
          }
        },
      }
    },
